body {
    overflow: auto;
    background: color;
    transition: background .5s ease;
}

.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: auto;
    margin-top: 250px;
    background: white;
    width: 80%;
    border-radius: 15px;
}

.icon {
    max-width: 50px;
    
}

.icons {
    display:flex;
    gap: 20px;
}

.icons img{ 
    cursor: pointer;
    height: 36px;
}

.quote {
    padding: 60px 110px;
    color: black; 
    font-size: 32px;
    text-align: center;
    
}

.line {
    width: 80%;
    height: 1.5px;
    background: black;
}

.bottom {
    width: 80%;
    
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin: 30px 0px;
}

.author {
    color: black;
    font-size: 20px;
    font-weight: 500;
}

@media screen and (max-width: 600px) {
    .quote {
        font-size: 24px;
        padding: 30px
    
    }
    .author {
        font-size: 16px;
    }
}